.project_details{
    width: 100vw;
    padding: 2em;
}

.project_details h1,
.project_details h2,
.project_details p{
    margin-bottom: 0.5em;
}

.project_details h1{
    background-color: #222;
    color: #ffbb33;
    padding: 0.4em;
}

.project_details span{
    font-weight: bold;
}

.details_container,
.service_form_container {
    border-bottom: 1px solid #7a7a7a;
    margin-bottom: 1.2em;
    padding-bottom: 1.2em;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.btn{
    display: flex;
    align-items: center;
    background-color: #222;
    height: 35px;
    color: #fff;
    padding: .5em .5em;
    text-decoration: none;
    transition: 0.5s;
    text-align: center;
    cursor: pointer;
    max-height: 40px;
    border: none;
    margin-top: 1em;
}

.btn:hover{
    color: #ffbb33;
}

.project_info{
    width: 100%;
}