.project_card {
  padding: 1em;
  border: 1px solid #7a7a7a;
  border-radius: 5px;
  flex-basis: calc(33.33% - 1%);
  margin: 0.5%;
}

@media (max-width: 768px) {
  .project_card {
    flex-basis: calc(50% - 1%);
  }

}
@media (max-width: 480px) {
  .project_card {
    flex-basis: 100%;
  }
}

.project_card h4 {
  background-color: #222;
  color: #ffbb33;
  padding: 0.6em;
  font-size: 1.3em;
  margin-bottom: 1.2em;
}

.project_card p {
  color: #7a7a7a;
  margin-bottom: 0.9em;
}

.project_card p span {
  font-weight: bold;
}

.category_text {
  display: flex;
  align-items: center;
}

.category_text span {
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #ccc;
  margin-right: 5px;
}

.category_text .infra {
  background-color: #ffaebc;
}

.category_text .desenvolvimento {
  background-color: #a0e7e5;
}

.category_text .design {
  background-color: #b4f8c8;
}

.category_text .planejamento {
  background-color: #fbe7c6;
}

.project_card_actions {
  margin-top: 1.2em;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.project_card_actions a,
.project_card_actions button {
  text-decoration: none;
  background-color: #fff;
  color: #222;
  font-size: 0.9em;
  padding: 0.6em 1em;
  margin-right: 1em;
  cursor: pointer;
  border: 1px solid #222;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.5s;
  margin-top: 0.5em;
}

@media(max-width: 768px){
    .project_card_actions{
        flex-direction: column;
    }
}

.project_card_actions svg {
  margin-right: 0.5em;
}

.project_card_actions a:hover,
.project_card_actions button:hover {
  background-color: #222;
  color: #ffbb33;
}
